#app {
  margin: auto;
  max-width: 920px;
  padding: 2em 0;
  letter-spacing: 0.04em;
}

.mk-general-item-title {
  margin: 0;
  color: #333;
  font-size: 1em;
  margin-bottom: 0;
}

.mk-general-item-sub-title {
  color: #777;
  display: block;
  font-size: 1em;
  margin-bottom: 0;
  text-decoration: none;
  display: inline-block;
}

.mk-item-wrapper__children {
  gap: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.mk-item-wrapper__children > * {
  flex: 1 1 auto;
  width: calc(50% - 1em);
}

.mk-link-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.mk-link-list__url {
  width: 100%;
}

.mk-link-list__icon {
  width: 0.9em;
  height: 0.9em;
  margin-right: 0.5em;
  filter: grayscale(1);
}

@media (max-width: 920px) {
  #app {
    padding: 2em 1.5em;
  }

  .mk-item-wrapper__children > * {
    flex: 1 1 100%;
  }

  .mk-link-list {
    align-items: baseline;
  }

  .mk-link-list__icon {
    width: 1em;
    height: 1em;
  }
}
